.App {
  text-align: center;
  padding: 20px;
}

.bug-container {
  margin: 20px 0;
}

.bug {
  font-size: 100px;
  background: none;
  border: none;
  cursor: pointer;
  transition: transform 0.1s;
}

.bug.animate {
  animation: clickAnimation 0.1s ease-in-out;
}

@keyframes clickAnimation {
  0% {
    transform: scale(1);
  }
  30% {
    transform: scale(0.7);
  }
  90% {
    transform: scale(1.1);
  }
  100% {
    transform: scale(1);
  }
}

.upgrades {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.upgrade-row {
  display: flex;
  align-items: center;
  margin-bottom: 10px;
}

/* Style for all buttons except the bug button */
button:not(.bug) {
  margin: 10px;
  padding: 10px;
  font-size: 16px;
  border-radius: 10px;
  border: 1px solid #ccc;
  background-color: #f0f0f0;
  cursor: pointer;
}

/* Hover effect for all buttons except the bug button */
button:not(.bug):hover:not(:disabled) {
  background-color: #e0e0e0;
}

/* Style for disabled buttons */
button:not(.bug):disabled {
  opacity: 0.6;
  cursor: not-allowed;
}



.sci-fi-button {
  position: relative;
  padding: 15px 30px;
  font-size: 18px;
  color: #00ffcc;
  background: none;
  border: 2px solid #00ffcc;
  border-radius: 10px;
  text-transform: uppercase;
  letter-spacing: 2px;
  cursor: pointer;
  transition: all 0.3s ease-in-out;
  overflow: hidden;
}

.sci-fi-button::before,
.sci-fi-button::after {
  content: '';
  position: absolute;
  width: 150%;
  height: 100%;
  background: linear-gradient(45deg, transparent, rgba(0, 255, 204, 0.5), transparent);
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%) rotate(45deg);
  transition: all 0.3s ease-in-out;
  z-index: -1;
}

.sci-fi-button::before {
  animation: rotate 6s linear infinite;
}

.sci-fi-button::after {
  animation: rotate 6s linear infinite reverse;
}

.sci-fi-button:hover {
  color: #1a1a1a;
  background: #00ffcc;
  box-shadow: 0 0 10px #00ffcc, 0 0 40px #00ffcc, 0 0 80px #00ffcc;
}

@keyframes rotate {
  0% {
    transform: translate(-50%, -50%) rotate(45deg);
  }
  100% {
    transform: translate(-50%, -50%) rotate(405deg);
  }
}